import React, { FC } from "react";
import { Background, Dashboard, RoomCard } from "./styled";
import { useSelector } from "react-redux";
import { CardState } from "../types/types";
import UseModal from "../modal/use-modal";
import { UilCube } from "@iconscout/react-unicons";

const Main: FC = () => {
  const cards = useSelector((state: CardState) => state.cards);

  return (
    <>
      <Background>
        <h1>Лаборатория</h1>
        <Dashboard>
          {cards.map((card, index) => (
            <RoomCard key={index}>
              <UilCube size="120" />
              {card.num}
              {card.name}
            </RoomCard>
          ))}
          <UseModal />
        </Dashboard>
      </Background>
    </>
  );
};
export default Main;
