import React, { FC, useState } from "react";
import { ICard } from "../types/types";
import {
  ActionCardButton,
  ActionCardDiv,
  AddForm,
  AddInput,
  NameCard,
  NameInput,
} from "./new-card-form.styled";

interface NewCardInputProps {
  addCard(card: ICard): void;
  onClose: () => void;
}

export const NewCardForm: FC<NewCardInputProps> = ({ addCard, onClose }) => {
  const [card, setCard] = useState<ICard>({ num: "", name: "" });

  const handleCardOnChange = (e: React.FormEvent<HTMLInputElement>) => {
    setCard({ ...card, [e.currentTarget.id]: e.currentTarget.value });
  };

  const addNewCard = (e: React.FormEvent) => {
    e.preventDefault();
    addCard(card);
    onClose();
    setCard({ num: "", name: "" });
  };

  return (
    <>
      <NameCard>
        <h1>Создание помещения</h1>
      </NameCard>
      <AddForm onSubmit={addNewCard}>
        <NameInput>Номер №</NameInput>
        <AddInput
          value={card.num}
          min="1"
          type="number"
          id="num"
          placeholder="Введите номер"
          onChange={handleCardOnChange}
          required
        />
        <NameInput>Название</NameInput>
        <AddInput
          value={card.name}
          type="text"
          id="name"
          placeholder="Введите название"
          onChange={handleCardOnChange}
          required
        />
        <ActionCardDiv>
          <ActionCardButton type="button" id="close" onClick={onClose}>
            Отменить
          </ActionCardButton>
          <ActionCardButton type="submit" id="save">
            Сохранить
          </ActionCardButton>
        </ActionCardDiv>
      </AddForm>
    </>
  );
};
