import React, { FC } from "react";
import { ModalShadow, ModalStyled } from "./modal.styled";
import { NewCardForm } from "../components/new-card-form";
import { addCard } from "../redux/actions";
import { useDispatch } from "react-redux";
import { ICard } from "../types/types";

interface ModalProps {
  onClose: () => void;
}

const Modal: FC<ModalProps> = ({ onClose }) => {
  const dispatch = useDispatch();

  const onAddCard = (card: ICard) => {
    dispatch(addCard(card));
  };

  const closeByEmpty = (e: React.MouseEvent) => {
    const target = e.target as Element;
    if (target.id === "shadow") onClose();
  };

  return (
    <>
      <ModalShadow id="shadow" onClick={closeByEmpty}>
        <ModalStyled>
          <NewCardForm addCard={onAddCard} onClose={onClose}></NewCardForm>
        </ModalStyled>
      </ModalShadow>
    </>
  );
};

export default Modal;
