import { CardAction, CardState, ICard, initialState } from "../types/types";
import { ADD_ARTICLE } from "./actions";

export const reduceCard = (
  state: CardState = initialState,
  action: CardAction
): CardState => {
  switch (action.type) {
    case ADD_ARTICLE:
      const newCard: ICard = {
        num: action.card.num,
        name: action.card.name,
      };
      return { ...state, cards: state.cards.concat(newCard) };
    default:
      return state;
  }
};
